<template>
  <div class="editMyCommunity">
    <div class="input">
      <van-field v-model="value"
                 label=""
                 class="userInput"
                 :border="false"
                 :placeholder="placeholder" />
      <div class="joinBtns">
        <van-button class="joinBtn"
                    block
                    type="info">{{btn}}</van-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      value: '',
      placeholder: '请输入好友钱包地址',
      btn: 'Join'
    }
  },
  mounted () {
    if (this.$route.query.name == 'join') {
      this.placeholder = '请输入社区名称'
      this.btn = '保存'
    } else {
      this.value = this.$route.query.name
    }
  }
}
</script>
<style lang="less" scoped>
.editMyCommunity {
  padding: 7px 10px;
  .input {
    background: #ffffff;
    border-radius: 10px;
    padding: 55px 10px 55px 10px;
    .userInput {
      height: 39px;
      background: #f6f6f6;
      border-radius: 3px;
    }
    .joinBtns {
      margin: 0 18px;
      margin-top: 30px;
      .joinBtn {
        height: 39px;
      }
    }
  }
}
</style>